em>\r\n              <!-- <p\r\n                class=\"pointer text-right\"\r\n                @click=\"registerVisible = true\"\r\n                style=\"width:100%; color:#001268; margin: 10px 0;\"\r\n              >注册</p> -->\r\n              <div class=\"space\"></div>\r\n              <div class=\"space\"></div>\r\n              <el-form-item>\r\n                <el-button\r\n                  type=\"primary\"\r\n                  @click=\"submitLogin('form')\"\r\n                  style=\"width:100%; \"\r\n                  :loading=\"loading\"\r\n                >{{buttonText}}</el-button>\r\n              </el-form-item>\r\n            </el-form>\r\n          </el-card>\r\n        </div>\r\n      </div>\r\n    </div>\r\n    <div class=\"footer f5 text-center\">\r\n      <!-- <img src=\"../assets/images/footer.png\" /> -->\r\n      技术支持：苏州惠邦医疗科技有限公司\r\n    </div>\r\n\r\n    <el-dialog\r\n      title=\"注册\"\r\n      :visible.sync=\"registerVisible\"\r\n      width=\"350px\"\r\n      center\r\n    >\r\n      <el-form\r\n        size=\"medium\"\r\n        label-width=\"100px\"\r\n        :model=\"formRegister\"\r\n      >\r\n        <el-form-item label=\"用户名：\">\r\n          <el-input v-model=\"formRegister.name\"></el-input>\r\n        </el-form-item>\r\n        <div class=\"space\"></div>\r\n        <el-form-item label=\"密 码：\">\r\n          <el-input v-model=\"formRegister.password\"></el-input>\r\n        </el-form-item>\r\n        <div class=\"space\"></div>\r\n        <el-form-item label=\"重复密码：\">\r\n          <el-input v-model=\"formRegister.comPas\"></el-input>\r\n        </el-form-item>\r\n      </el-form>\r\n      <span\r\n        slot=\"footer\"\r\n        class=\"dialog-footer\"\r\n      >\r\n        <el-button\r\n          @click=\"registerVisible = false\"\r\n          size=\"small\"\r\n        >取 消</el-button>\r\n        <el-button\r\n          size=\"small\"\r\n          type=\"primary\"\r\n          @click=\"handleRegister\"\r\n        >提 交</el-button>\r\n      </span>\r\n    </el-dialog>\r\n  </div>\r\n\r\n</template>\r\n\r\n<script>\r\nimport { Login } from '@/api/auth'\r\n\r\nexport default {\r\n  data() {\r\n    return {\r\n      registerVisible: false,\r\n      loading: false,\r\n      buttonText: '立即登录',\r\n      form: {\r\n        name: '',\r\n        password: ''\r\n      },\r\n      rules: {\r\n        name: [{\r\n          required: true,\r\n          message: '请输入工号',\r\n          trigger: 'blur'\r\n        }],\r\n        password: [{\r\n          required: true,\r\n          message: '请输入密码',\r\n          trigger: 'blur'\r\n        }]\r\n      },\r\n      formRegister: {\r\n        name: '',\r\n        password: '',\r\n        comPas: ''\r\n      }\r\n    }\r\n  },\r\n\r\n  methods: {\r\n    handleRegister() {\r\n      // todo 注册事件\r\n      // if (success)\r\n      this.$alert('恭喜！注册成功！请等待管理员审核。', '提示', {\r\n        confirmButtonText: '确定'\r\n      })\r\n    },\r\n    submitLogin(formName) {\r\n      this.$refs[formName].validate((valid) => {\r\n        if (valid) {\r\n          this.loading = true\r\n          this.buttonText = '正在登录...'\r\n          this.handleLogin()\r\n        } else {\r\n          console.log('error submit!!')\r\n        }\r\n      })\r\n    },\r\n    async handleLogin() {\r\n      const res = await Login(this.form)\r\n      this.loading = false\r\n      this.buttonText = '立即登录'\r\n      if (res.code === 0) {\r\n        this.$toast('登录成功！')\r\n        localStorage.setItem('user', JSON.stringify(this.form))\r\n        localStorage.setItem('token', res.result.token)\r\n        this.$router.push({ path: this.$urls.REPORT })\r\n      } else {\r\n        this.$toast(res.msg)\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n</script>\r\n\r\n<style lang=\"less\" scoped>\r\n.wrap {\r\n  width: 100%;\r\n  height: 100%;\r\n\r\n  &-cont {\r\n    flex: 1;\r\n    align-items: center;\r\n    background: url(../assets/images/bg.jpg) no-repeat center;\r\n    background-size: 100% 100%;\r\n  }\r\n\r\n  .footer {\r\n    width: 100%;\r\n    height: 50px;\r\n    background: #313131;\r\n    color: #fff;\r\n    line-height: 50px;\r\n    img {\r\n      display: block;\r\n      margin: auto;\r\n    }\r\n  }\r\n}\r\n.box {\r\n  flex: 1;\r\n\r\n  &-cont {\r\n    background: rgba(255, 255, 255, 0.3);\r\n  }\r\n\r\n  &-card {\r\n    margin: auto;\r\n    width: 360px;\r\n    background: none;\r\n    border: none;\r\n    box-shadow: none;\r\n\r\n    .form-item {\r\n      position: relative;\r\n      img {\r\n        position: absolute;\r\n        top: 6px;\r\n        left: 10px;\r\n        z-index: 1;\r\n        width: 20px;\r\n        padding-right: 6px;\r\n        border-right: 1px solid #dcdcdc;\r\n      }\r\n    }\r\n  }\r\n}\r\n</style>\r\n\r\n<style>\r\n.form-item .el-input__inner {\r\n  padding-left: 46px !important;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

